<template>
	<div id="paotui-detail">
		<public-header title="订单详情"></public-header>
		<div class="content">
			<router-link tag="div" class="order-log padding-15 bg-default font-15" :to="util.getUrl({path: 'pages/plugin/paotui/log', query: {id: order.id}})">
				<van-row class="flex-lr c-gray">
					<van-col span="2">
						<div class="icon icon-waimai font-20 c-primary"></div>
					</van-col>
					<van-col span="21" v-if="order.log_current">
						<div class="c-primary">{{order.log_current.title}}, 操作人: {{order.log_current.role_cn}}</div>
						<div class="font-13 margin-5-t">{{order.log_current.addtime_cn}}</div>
					</van-col>
					<van-col span="1">
						<div class="icon icon-right "></div>
					</van-col>
				</van-row>
			</router-link>
			<div class="tip">订单配送</div>
			<div class="address bg-default">
				<div class="type van-hairline--bottom c-gray font-15" v-if="order.deliveryer_id > 0">{{order.delivery_collect_type_cn}}</div>
				<div class="address-detail">
					<div class="address-item buy flex-lr margin-15-b">
						<div class="left">
							<div class="circle"></div>
						</div>
						<div class="right">
							<div class="ellipsis">{{order.order_type == 'buy' ? '购买' : '取货'}}地址：{{order.buy_address}}</div>
							<div class="ellipsis margin-10-t c-gray font-14" v-if="order.order_type == 'delivery'">{{order.buy_username}} {{order.buy_mobile}}</div>
						</div>
					</div>
					<div class="address-item accept flex-lr">
						<div class="left">
							<div class="circle"></div>
						</div>
						<div class="right">
							<div class=" ellipsis">收货地址</div>
							<div class=" ellipsis margin-10-t c-gray font-14">收货人：{{order.accept_username}} {{order.accept_mobile}}</div>
						</div>
					</div>
				</div>
			</div>
			<div class="telephone flex-lr bg-default margin-10-t padding-10-tb ">
				<div class="tel-item van-hairline--right" @click="util.jsTel(order.buy_mobile)" v-if="order.order_type == 'delivery'">
					<img src="static/img/store.png" alt="">
					<span>{{order.buy_mobile}}</span>
				</div>
				<div class="tel-item" @click="util.jsTel(order.accept_mobile)">
					<img src="static/img/customer.png" alt="">
					<span>{{order.accept_mobile}}</span>
				</div>
			</div>
			<template v-if="order.deliveryer_id > 0 && order.deliveryer">
				<div class="tip">配送员信息</div>
				<van-row class="deliveryer-info bg-default padding-15-lr padding-10-tb flex-lr">
					<van-col span="4">
						<img :src="order.deliveryer.avatar" alt="">
					</van-col>
					<van-col span="12" class="padding-10-l">
						<div>{{order.deliveryer.title}}</div>
						<div class="c-gray font-14 margin-10-t ">{{order.deliveryer.mobile}}</div>
					</van-col>
					<van-col span="4" class="text-right">
						<div class="icon icon-telephone font-18" @click="util.jsTel(order.deliveryer.mobile)"></div>
					</van-col>
					<van-col span="4" class="text-right">
						<router-link tag="div" :to="util.getUrl({path: '/pages/plugin/paotui/location', query: {id: order.deliveryer.id}})" class="icon icon-location"></router-link>
					</van-col>
				</van-row>
			</template>
			<div class="tip">货品信息</div>
			<van-cell-group class="goods-info">
				<van-cell class="border-0px">
					<div slot="title">配送时间：{{order.delivery_time}}</div>
				</van-cell>
				<div class="note">
					<div class="left">备注:</div>
					<div class="right">{{order.note}}</div>
				</div>
				<div class="other padding-15-b font-14 c-gray">
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>物品名称</div>
						<div>{{order.goods_name}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr" v-if="order.goods_price > 0">
						<div>物品预期价格</div>
						<div>¥{{order.goods_price}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr" v-if="order.goods_weight > 0">
						<div>物品重量</div>
						<div>{{order.goods_weight}}kg</div>
					</div>
					<template v-if="order.data && order.data.order">
						<template v-if="order.data.order.partData" v-for="item in order.data.order.partData">
							<div class="other-item margin-15-lr margin-15-t flex-lr">
								<div>{{item.title}}</div>
								<div v-if="item.type == 'multipleChoices'">
									<template v-for="da in item.value">
										{{' ' + da}}
									</template>
								</div>
								<div v-else>{{item.value}}</div>
							</div>
						</template>
					</template>
				</div>
			</van-cell-group>
			<template v-if="order.thumbs && order.thumbs.length > 0">
				<div class="tip">顾客上传的图片</div>
				<van-row gutter="10" class="thumbs bg-default">
					<van-col span="6" v-for="(item, index) in order.thumbs" :key="index">
						<div @click="util.jsPreviewImage(order.thumbs, index)">
							<img :src="item" alt="">
						</div>
					</van-col>
				</van-row>
			</template>
			<template v-if="order.data && order.data.order">
				<template v-if="order.data.order.thumbs" v-for="item in order.data.order.thumbs">
					<div class="tip">{{item.title}}</div>
					<van-row gutter="10" class="thumbs bg-default">
						<van-col span="6" v-for="(imgItem, index) in item.value" :key="index">
							<div @click="util.jsPreviewImage(item.value, index)">
								<img :src="imgItem" alt="">
							</div>
						</van-col>
					</van-row>
				</template>
			</template>
			<template v-if="order.extra_fee && order.extra_fee.length > 0">
				<div class="tip">附加服务</div>
				<van-cell-group class="order-info">
					<template v-for="item in order.extra_fee">
						<van-cell :title="item.title" :value="item.text" class="border-0px" />	
					</template>
				</van-cell-group>
			</template>
			<div class="tip">订单信息</div>
			<van-cell-group class="order-info">
				<van-cell title="订单编号" :value="order.order_sn" class="border-0px" />
				<van-cell title="下单时间" :value="order.addtime_cn" class="border-0px" />
				<van-cell title="支付方式" :value="order.pay_type_cn" />
			</van-cell-group>
			<div class="tip">订单收入</div>
			<van-cell-group class="goods-info">
				<div class="other padding-15-b font-14 c-gray">
					<div class="other-item margin-15-lr padding-15-t flex-lr">
						<div>配送费</div>
						<div>¥{{order.delivery_fee}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>小费</div>
						<div>¥{{order.delivery_tips}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr" v-if="0">
						<div>其他费</div>
						<div>¥{{order.deliveryer_fee}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>合计</div>
						<div>¥{{order.total_fee}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>优惠</div>
						<div>¥{{order.discount_fee}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>顾客实付</div>
						<div>¥{{order.final_fee}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>代理商收入</div>
						<div>¥{{order.plateform_serve_fee}}</div>
					</div>
					<template v-if="order.agentid > 0">
						<div class="other-item margin-15-lr margin-15-t flex-lr">
							<div>平台服务佣金(平台收取代理商)</div>
							<div>¥{{order.agent_serve_fee}}</div>
						</div>
						<div class="other-item margin-15-lr margin-15-t flex-lr">
							<div>代理商实际收入</div>
							<div>¥{{order.agent_final_fee}}</div>
						</div>
					</template>
				</div>
			</van-cell-group>
			<van-row class="btn-group bg-default">
				<template v-if="order.status < 3">
					<van-col span="8" v-if="order.status == 1">
						<router-link tag="div" :to="util.getUrl({path: 'pages/plugin/paotui/dispatch', query: {id: order.id}})" class="btn-item bg-primary">调度</router-link>
						<div class="btn-item bg-primary" @click="onChangeOrderStatus(order, 'dispatch')">调度</div>
					</van-col>
					<van-col span="8" v-if="order.status == 2">
						<router-link tag="div" :to="util.getUrl({path: 'pages/plugin/paotui/dispatch', query: {id: order.id}})" class="btn-item bg-primary">重新调度</router-link>
					</van-col>
					<van-col span="8" v-if="order.status == 2">
						<div class="btn-item bg-primary" @click="onChangeOrderStatus(order, 'end')">完成订单</div>
					</van-col>
					<van-col span="8" v-if="order.is_pay == 1 && order.pay_type != 'delivery'">
						<div class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'cancel_refund')">取消订单并退款</div>
					</van-col>
					<van-col span="8" v-else>
						<div class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'cancel')">取消订单</div>
					</van-col>
				</template>
				<van-col span="8" v-if="order.refund_status == 1">
					<div class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_handle')">发起退款</div>
					<div class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_status')">已退款</div>
				</van-col>
				<van-col span="8" v-else-if="order.refund_status == 2">
					<div class="btn-item bg-primary" @click="onChangeOrderStatus(order, 'refund_query')">查询退款进度</div>
					<div class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_status')">已退款</div>
				</van-col>
			</van-row>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {changePaotuiOrderStatus} from "./controller/errander.js"
	export default {
		data() {
			return {
				showPreLoading: true,
				order: {}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'plateform/errander/order/detail',
					data: {
						id: this.$route.query.id
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.order = result.message.order;
				})
			},
			onChangeOrderStatus: function(order, type) {
				var params = {order: order, type: type, from: 'detail', vue: this};
				changePaotuiOrderStatus(params);
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#paotui-detail .thumbs{
		padding: 15px 15px 5px;
	}
	#paotui-detail .thumbs .van-col{
		font-size: 0px;
	}
	#paotui-detail .thumbs img{
		width: 100%;
		height: 60px;
		margin-bottom: 10px;
		font-size: 0px;
	}
	#paotui-detail .tip{
		padding: 10px 20px;
		font-size: 14px;
		color: #7a7a7a;
	}
	#paotui-detail .address .type{
		padding: 10px 15px;
	}
	#paotui-detail .address .address-detail{
		padding: 15px;
	}
	#paotui-detail .address .address-detail .address-item .left{
		width: 6%;
	}
	#paotui-detail .address .address-detail .address-item .right{
		width: 94%;
	}
	#paotui-detail .address .address-detail .address-item .circle{
		width: 10px;
		height: 10px;
		border-radius: 50%;
	}
	#paotui-detail .address .address-detail .address-item.buy .circle{
		background-color: #E87613;
	}
	#paotui-detail .address .address-detail .address-item.accept .circle{
		background-color: #4FAE52;
	}
	#paotui-detail .telephone .tel-item{
		width: 50%;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		padding: 10px 0;
	}
	#paotui-detail .telephone .tel-item img{
		width: 24px;
		height: 24px;
		margin-right: 5px;
	}
	#paotui-detail .deliveryer-info img{
		width: 50px;
		height: 50px;
		border-radius: 50%;
	}
	#paotui-detail .deliveryer-info .icon-location{
		width: 40px;
		height: 40px;
		line-height: 40px;
		border: 1px solid #e7e7e7;
		border-radius: 100%;
		background-color: #fff;
		color: #656565;
		text-align: center;
		font-size: 20px;
	}
	#paotui-detail .goods-info .note{
		background-color: #FECDAA;
		padding: 10px 15px;
		color: #7a7a7a;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		line-height: 1.4;
		font-size: 14px;
	}
	#paotui-detail .goods-info .note .left{
		width: 12%;
	}
	#paotui-detail .goods-info .note .right{
		width: 88%;
	}
	#paotui-detail .goods-info .goods-item .goods-title{
		width: 60%;
	}
	#paotui-detail .goods-info .goods-item .goods-num{
		width: 10%;
	}
	#paotui-detail .goods-info .goods-item .goods-price{
		width: 30%;
		text-align: right;
	}
	#paotui-detail .btn-group{
		width: 100%;
		padding: 7.5px;
	}
	#paotui-detail .btn-group .btn-item{
		margin: 7.5px;
		padding: 12px 0;
		border-radius: 5px;
		text-align: center;
		font-size: 14px;
	}
</style>


